"use client";

import { useEffect, useState, use } from "react";
import { useSearchParams, useRouter } from "next/navigation";
import { ArrowLeft, Building, User, Briefcase, Users } from "lucide-react";
import { Button } from "../../../components/ui/button";
import { Card } from "../../../components/ui/card";
import { motion } from "framer-motion";

interface Job {
  id: string;
  company_name: string;
  job_title?: string;
  role_title?: string;
  description?: string | null;
  owner_role?: string | null;
  eligibleCandidatesCount: number;
  bestMatchScore: number | null;
}

export default function HyperconnectorJobsHomePage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const token = searchParams.get("token");

  const [loading, setLoading] = useState(true);
  const [jobs, setJobs] = useState<Job[]>([]);
  const [hyperconnectorId, setHyperconnectorId] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!token) {
      setError("Token no proporcionado");
      setLoading(false);
      return;
    }

    async function fetchJobs() {
      try {
        const response = await fetch(`/api/hyperconnector/${token}/jobs`);
        if (!response.ok) {
          const errorData = await response.json();
          setError(errorData.error || "Error al cargar los jobs");
          return;
        }
        const result = await response.json();
        setJobs(result.jobs || []);
        setHyperconnectorId(result.hyperconnectorId || null);
      } catch (err: any) {
        setError("Error al cargar los jobs");
        console.error(err);
      } finally {
        setLoading(false);
      }
    }

    fetchJobs();
  }, [token]);

  const getMatchColor = (score: number | null) => {
    if (!score) return "bg-gray-500";
    if (score >= 90) return "bg-green-500";
    if (score >= 75) return "bg-yellow-500";
    return "bg-red-500";
  };

  const handleJobClick = async (jobId: string) => {
    if (!token || !hyperconnectorId) return;
    
    try {
      // Generar nuevo token para este job
      const newTokenResponse = await fetch(`/api/hyperconnector/generate-token`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          hyperconnectorId,
          jobId,
        }),
      });
      
      if (newTokenResponse.ok) {
        const { token: newToken } = await newTokenResponse.json();
        router.push(`/recommend/${newToken}`);
      } else {
        console.error("Error generating token");
      }
    } catch (error) {
      console.error("Error generating token:", error);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-900 via-purple-900 to-indigo-900">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto"></div>
          <p className="mt-4 text-white">Cargando solicitudes...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-900 via-purple-900 to-indigo-900">
        <div className="text-center">
          <div className="text-red-300 text-xl mb-4">⚠️</div>
          <p className="text-white text-lg">{error}</p>
          {token && (
            <Button
              onClick={() => router.push(`/recommend/${token}`)}
              className="mt-4 bg-[#0047ff] hover:bg-[#0047ff]/90 text-white"
            >
              Volver
            </Button>
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen px-4 py-12 relative bg-gradient-to-br from-blue-900 via-purple-900 to-indigo-900">
      <div className="max-w-6xl mx-auto space-y-8 relative z-10">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="flex items-center justify-between"
        >
          <Button
            onClick={() => {
              if (token) {
                router.push(`/recommend/${token}`);
              }
            }}
            variant="outline"
            className="gap-2 h-12 px-6 rounded-2xl border border-white text-white bg-transparent hover:bg-white/10 backdrop-blur-[130px]"
          >
            <ArrowLeft className="h-4 w-4" />
            Volver
          </Button>
          <h1 className="text-white text-3xl font-semibold">Talent Cupid Hub</h1>
          <div className="w-32"></div> {/* Spacer for centering */}
        </motion.div>

        {/* Jobs List */}
        {jobs.length === 0 ? (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center py-12"
          >
            <p className="text-white/80 text-lg">
              No hay solicitudes disponibles con candidatos elegibles en este momento.
            </p>
          </motion.div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {jobs.map((job, index) => {
              const jobTitle = job.job_title || job.role_title || "Posición";
              return (
                <motion.div
                  key={job.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: index * 0.1 }}
                >
                  <Card
                    onClick={() => handleJobClick(job.id)}
                    className="overflow-hidden backdrop-blur-[130px] bg-white/30 border border-white/40 shadow-[8px_8px_30px_0px_rgba(0,0,0,0.06)] hover:bg-white/40 transition-all duration-300 rounded-2xl cursor-pointer"
                  >
                    <div className="p-6 space-y-4">
                      {/* Job Title */}
                      <h3 className="text-white text-xl font-semibold line-clamp-2">
                        {jobTitle}
                      </h3>

                      {/* Company */}
                      {job.company_name && (
                        <div className="flex items-center gap-2 text-white/80">
                          <Building className="h-4 w-4" />
                          <span>{job.company_name}</span>
                        </div>
                      )}

                      {/* Owner Role */}
                      {job.owner_role && (
                        <div className="flex items-center gap-2 text-white/80">
                          <Briefcase className="h-4 w-4" />
                          <span>{job.owner_role}</span>
                        </div>
                      )}

                      {/* Stats */}
                      <div className="flex items-center justify-between pt-4 border-t border-white/20">
                        <div className="flex items-center gap-2 text-white/80">
                          <Users className="h-4 w-4" />
                          <span className="text-sm">
                            {job.eligibleCandidatesCount} candidato{job.eligibleCandidatesCount !== 1 ? "s" : ""}
                          </span>
                        </div>
                        {job.bestMatchScore !== null && (
                          <div className="flex items-center gap-2 backdrop-blur-sm bg-white/20 rounded-xl px-4 py-2 border border-white/30">
                            <div
                              className={`h-3 w-3 rounded-full ${getMatchColor(job.bestMatchScore)}`}
                            />
                            <span className="text-white font-semibold text-sm">
                              {Math.round(job.bestMatchScore)}% Match
                            </span>
                          </div>
                        )}
                      </div>

                      {/* Description preview */}
                      {job.description && (
                        <p className="text-white/70 text-sm line-clamp-2">
                          {job.description}
                        </p>
                      )}
                    </div>
                  </Card>
                </motion.div>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}

